.clock-body {
  text-align: center;
  background: url("forest-conservation-drones.16442e31.jpg") 0 0 / cover no-repeat;
  font-size: 20px;
}

.clock-main {
  min-height: 100vh;
  flex: 1;
  align-items: center;
  font-size: 2rem;
  display: flex;
}

.clock {
  width: 30rem;
  height: 30rem;
  border: 1px solid #fff;
  border-radius: 50%;
  margin: 50px auto;
  padding: 2rem;
  position: relative;
  box-shadow: 0 0 0 4px rgba(0, 0, 0, .1), inset 0 0 0 3px #efefef, inset 0 0 10px #000, 0 0 10px rgba(0, 0, 0, .2);
}

.clock-face {
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateY(-3px);
}

.hand {
  width: 50%;
  height: 3px;
  transform-origin: 100%;
  background-color: #fff;
  border-radius: 3px;
  transition: all 50ms cubic-bezier(.1, 2.7, .58, 1);
  position: absolute;
  top: 50%;
  transform: rotate(90deg);
}

.min-hand {
  width: 45%;
  height: 4px;
  top: 50%;
  left: 5%;
}

.hour-hand {
  width: 35%;
  height: 5px;
  top: 50%;
  left: 15%;
}

